import React, {Component} from 'react';
import AlbumList from './../AlbumList';
import spotifyApi from './../../api/Spotify'

import './artist-info.css';

class ArtistInfo extends Component {

    constructor(props) {
        super(props);
        this.state = {
            artist: null,
            albums: [],
            error: false,
            message: 'Carregando...'
        }
    }

    componentDidMount() {
        let {id} = this.props.match.params;

        spotifyApi.getArtistAlbums(id).then((albums) => {
            this.setState({albums: albums.items});
        }, (e) => {
            this.setState({error: true, message: 'Erro!'});
        });

        spotifyApi.getArtist(id).then((artist) => {
            this.setState({artist: artist});

        }, (e) => {
            this.setState({error: true, message: 'Erro!'});
        });
    }

    render() {
        if (!this.state.artist || this.state.error) {
            return <p>{this.state.message}</p>;
        }
        let artist = this.state.artist;
        let albums = this.state.albums;
        console.log('Abums', albums);
        return (
            <div key={artist.id}>
                <h2>{artist.name}</h2>
                <AlbumList albuns={albums}/>
            </div>
        );
    }
}

export default ArtistInfo;


