import React, {Component} from 'react';
import AlbumList from './../componentes/AlbumList'
import spotifyApi from './../api/Spotify';

class HomePage extends Component {

    constructor() {
        super();
        this.state = {albuns: null};
    }

    componentDidMount() {
        spotifyApi.getArtistAlbums('3dRfiJ2650SZu6GbydcHNb')
            .then((data) => {
                this.setState({albuns: data.items});
            });
    }

    render() {

        if (!this.state.albuns) {
            return <p>Caregando...</p>;
        }

        return (
            <AlbumList albuns={this.state.albuns}/>
        );
    }
}

export default HomePage;



