import React, {Component} from 'react';
import AlbumCard from './album-card/AlbumCard';

class AlbumList extends Component {
    constructor(props) {
        super(props);
        this.albunsList = this.props.albuns ? this.props.albuns : [];
    }

    render() {
        if (!this.albunsList.length) {
            return <p>Nenhum álbum encontrado.</p>;
        }
        const albumList = this.albunsList.map((album, index) => {
            return (
                <div key={index}>
                    <AlbumCard album={album}/>
                </div>
            );
        });

        return (
            <div>
                {albumList}
            </div>
        );
    }
}

export default AlbumList;



