import React, {Component} from 'react';
import darkBaseTheme from 'material-ui/styles/baseThemes/darkBaseTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import {BrowserRouter as Router, Route, Link} from 'react-router-dom';
import HomePage from './pages/HomePage';
import AlbumInfo from './componentes/album-info/AlbumInfo';
import ArtistInfo from './componentes/artist-info/ArtistInfo';
import CampoBusca from './componentes/CampoBusca';
import logo from './logo.svg';
import './App.css';
import appConfig from './AppConfig';

class App extends Component {
    render() {
        return (
            <Router>
                <MuiThemeProvider muiTheme={getMuiTheme(darkBaseTheme)}>
                    <div data-reactroot>
                        <header className="App-header">
                            <img src={logo} className="App-logo" alt="logo"/>
                            <h1 className="App-title">
                                <div className="menu-link">
                                    <Link to={appConfig.createUrl()}>Playsic</Link>
                                </div>
                            </h1>
                            <CampoBusca/>
                        </header>
                        <div className="App-intro">
                            <div>
                                <Route exact path={appConfig.createUrl()} component={HomePage}/>
                                <Route path={appConfig.createUrl('albums/:id')} component={AlbumInfo}/>
                                <Route path={appConfig.createUrl('artists/:id')} component={ArtistInfo}/>
                            </div>
                        </div>
                    </div>
                </MuiThemeProvider>
            </Router>
        );
    }
}

export default App;



