class AppConfig {
    constructor() {
        this.baseUrl = 'http://localhost:3000';
        this.url = '/';
        if (process.env.NODE_ENV === "production") {
            this.url = '/playsic';
            this.baseUrl = 'https://lvrochaa.github.io';
        }
    }

    createUrl(url = ''){
        return this.url + url;
    }

    createBaseUrl(url = ''){
        return this.baseUrl + this.createUrl(url);
    }
}

export default new AppConfig();


