import React, {Component} from 'react';
import {Card, CardActions, CardMedia, CardText} from 'material-ui/Card';
import FlatButton from 'material-ui/FlatButton';
import {Link} from './../../../node_modules/react-router-dom';
import appConfig from './../../AppConfig';
import noImage from './no_image.png';

import './album-card.css'

class AlbumCard extends Component {
    constructor(props) {
        super(props);

        this.artists = this.props.album.artists[0];
        this.name = this.props.album.name;
        this.id = this.props.album.id;
        this.urlImg = this.props.album.images[1] ? this.props.album.images[1].url : noImage;
    }

    render() {
        let nomeArtista = this.artists.name;
        let nomeAlbum = this.name;
        let idAlbum = this.id;
        let urlImg = this.urlImg;
        return (
            <Link to={appConfig.createUrl('albums/' + idAlbum)}>
                <div className="a-coluna a-per-colum">
                    <Card className="a-card">
                        <CardMedia>
                            <div className="album-card_img">
                                <img src={urlImg} alt=""/>
                            </div>
                        </CardMedia>
                        <CardText className="a-info">
                            {nomeArtista} <br/><br/>
                            {nomeAlbum}
                        </CardText>
                        <CardActions>
                            <FlatButton label="Play"/>
                        </CardActions>
                    </Card>
                </div>
            </Link>
        );
    }
}

export default AlbumCard;



