import SpotifyWebApi from 'spotify-web-api-js/src/spotify-web-api';
import appConfig from './../AppConfig';
import axios from './../../node_modules/axios';

class Spotify extends SpotifyWebApi {

    constructor() {
        super();
        this.token = '';
        this.code = '';
        this.client_id = '821acfdf1f9e4116ad4a56a4eb8506a8';
        this.client_secret = 'f04bf4b832464e40ad40e1029b0cc738';
        this.redirect_uri = appConfig.createBaseUrl();
        this.spotifyApi = new SpotifyWebApi();

        this.getImplicitGrantFlow();
    }

    getAuthorizationCodeFlow() {
        const authOptions = {
            headers: {
                'Authorization': 'Basic ' + (new Buffer(this.client_id + ':' + this.client_secret).toString('base64'))
            },
            params: {
                grant_type: 'client_credentials',
                code: this.code,
                redirect_uri: this.redirect_uri
            },
            json: true
        };
        axios.post('https://accounts.spotify.com/api/token', authOptions).then((data)=>{
            console.log(data);
            this.setState({token: data.access_token})
        },(e)=>{
            console.log(e);
        });
    }

    getCode() {
        if (Spotify.getUrlValue('code') !== null) {
            this.code = Spotify.getUrlValue('code');
            this.getAuthorizationCodeFlow();
        }

        this.spotifyApi.getArtistAlbums('3dRfiJ2650SZu6GbydcHNb')
            .then((data) => {
            },(e) => {
                // window.location = 'https://accounts.spotify.com/authorize/?client_id=' + (this.client_id) + '&redirect_uri=' + encodeURIComponent(this.redirect_uri) + '&response_type=code';
            });
    }

    getImplicitGrantFlow() {
        if (Spotify.getUrlValue('access_token') !== null) {
            this.token = Spotify.getUrlValue('access_token');
            this.spotifyApi.setAccessToken(this.token);
        }

        this.spotifyApi.getArtistAlbums('3dRfiJ2650SZu6GbydcHNb')
            .then((data) => {
            },(e) => {
                window.location = 'https://accounts.spotify.com/authorize/?client_id=' + (this.client_id) + '&redirect_uri=' + encodeURIComponent(this.redirect_uri) + '&response_type=token';
            });
    }

    static getUrlValue(key) {
        let url_string = window.location;
        let matches = url_string.hash.match(new RegExp(key + '=([^&]*)'));
        let ret = matches ? matches[1] : null;

        if(ret){
            return ret;
        }

        let url = new URL(url_string);
        let c = url.searchParams.get(key);
        return c ? c : null;
    }
}

export default new Spotify();


